/* styles.css */
.table-container {
    width: 100%;
    overflow-x: auto;
  }
  
  .table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
  }
  
  .table th, .table td {
    padding: 12px 15px;
    border: 1px solid #ddd;
    text-align: left;
  }
  
  .table th {
    background-color: #f4f4f4;
  }
  
  @media (max-width: 600px) {
    .table th, .table td {
      display: block;
      width: 100%;
    }
  
    .table th {
      position: absolute;
      top: -9999px;
      left: -9999px;
    }
  
    .table td {
      position: relative;
      padding-left: 50%;
    }
  
    .table td::before {
      content: attr(data-label);
      position: absolute;
      left: 0;
      width: 50%;
      padding-left: 15px;
      font-weight: bold;
      background-color: #f4f4f4;
    }
  }


  /* styles.css */
.list-container {
    margin: 20px 0;
    color: #0a0a23;
  }
  
  .list-container ol, .list-container ul {
    margin: 0;
    padding: 0;   
  }

  .list-container ul,
  .list-container ol {
    list-style: none !important; 
  } 
  .list-container ul li,
  .list-container ol li {
    position: relative;
  }
  .list-container ul li::before {
    content: '';
    position: absolute;
    left: -20px;
    top: 50%; 
    width: 6.2px;
    height: 6.2px;
    background-color:#0a0a23;   
    display: block;
    border-radius: 50%;
    transition: transform 0.3s ease, background-color 0.3s ease, outline 0.3s ease;
  }
  .list-container ol li {
    counter-increment: item;
    margin-left: 35px !important;
    
    }
  .list-container ol li::before {
    font-size: 12px !important;
    content: counter(item);
    position: absolute;
    left: -35px;
    top: calc(50% - 12px);
    width: 24px;
    height: 24px;
    line-height: 24px;
    border-radius: 50%;
    background-color: rgba(211, 211, 211, 0.5);
    color: #0a0a23;
    font-weight: bold;
    text-align: center;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  

  .list-container ul li:hover::before {
    background-color:#b9355e;  
  }

  .list-container ol li:hover::before {
    background-color:#b9355e; 
    color: #fff;
  }
  
  .list-container ol {
    list-style-type: decimal;
  }
  
  .list-container ul {
    list-style-type: disc;
  }

  .list-container ul li, 
  .list-container ol li {
    margin-bottom: 6px; 
    margin-left: 20px;
    font-size: 16px;
  }
  
  @media (max-width: 600px) {
    .list-container li {
      font-size: 14px;
    }
  }

