 .searched-items {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
}

/* Style for each list item */
.searched-items li {
    text-align: left;
    flex-grow:1;
    flex-shrink:1;
    flex-basis:calc(33.333% - 10px); /* Three columns */
    margin: 5px; 
    padding: 10px; 
    box-sizing: border-box; /* Ensure padding and border are included in the width */
    flex-direction: column; 
    height: auto; 
    
    display: inline-block;
}
.searched-items li:not(.contain-li-ads) {
    border: 1px solid #17182c;
    background-color: #f9f9f9;
    border-radius: 5px;
}
.searched-items li:not(.contain-li-ads):before {
    content: '';
    width: 6px;
    height: 6px;
    background-color: rgb(95, 95, 95);
}

.searched-items li a {
    font-size: 12px;
    color: #1d2031;
    font-weight: bold;  
}
 
.searched-items li:hover a {
    color: #b9355e;
} 


/* Responsive design */
@media (max-width: 900px) {
    .searched-items li {
        flex-grow:1;
        flex-shrink:1;
        flex-basis:calc(50% - 10px); /* Two columns for medium screens */
    }
}

@media (max-width: 600px) {
    .searched-items li {
        flex: 1 1 100%; /* Single column for small screens */
    }
}

 .searched-items li a {
    display: block;
 }

